import FormItem from './FormItem';
import { useState, useRef, useEffect } from 'react';
import { matchesLabel } from '../../scripts/id-util.js';
import SearchResults from './SearchResults.js';

const FormBlock = (props) => {
    const { form } = props;
    const info = form.criticalActions[props.block]
    const [ numGreen, setNumGreen ] = useState(1);
    const [ numYellow, setNumYellow ] = useState(1);
    const [ numRed, setNumRed ] = useState(1);
    const [ numFindings, setNumFindings ] = useState(1);
    const [ numNotes, setNumNotes ] = useState(1);

    const newGreen = () => setNumGreen(numGreen + 1);
    const newYellow = () => setNumYellow(numYellow + 1);
    const newRed = () => setNumRed(numRed + 1);
    const newFindings = () => setNumFindings(numFindings + 1);
    const newNotes = () => setNumNotes(numNotes + 1);

    const lessGreen = () => setNumGreen(numGreen - 1);
    const lessYellow = () => setNumYellow(numYellow - 1);
    const lessRed = () => setNumRed(numRed - 1);
    const lessFindings = () => setNumFindings(numFindings - 1);
    const lessNotes = () => setNumNotes(numNotes - 1);

    const [beforeQuery, setBeforeQuery] = useState("");
    const [afterQuery, setAfterQuery] = useState("");
    const beforeRef = useRef("");
    const afterRef = useRef("");

    const setBefore = (e) => {
        beforeRef.current.value = e.target.id;
        setBeforeQuery("");
    }
    const setAfter = (e) => {
        afterRef.current.value = e.target.id;
        setAfterQuery("");
    }

    useEffect(() => {
        if (form) {
            let info = form.criticalActions[props.block];
            setNumGreen(info.standard.length)
            setNumYellow(info.minimal.length)
            setNumRed(info.contraindicated.length + info.required.length)
            setNumFindings(info.findings.length)
            setNumNotes(info.notes.length)
        }
    }, [form, props.block])

    return (
        <div className="mdb-block-wrapper">
            <div id="mdb-info" className="mdb-block" >
                <p className="mdb-block-heading" >Info:</p>
                <div className="mdb-info-wrapper">
                    <label>
                        Critical action:
                        <input type="text" 
                            className="mdb-text" 
                            id={`${props.block}-label`} 
                            autoComplete={"off"} 
                            defaultValue={info.label} 
                        />
                    </label>
                    <label>
                        Protocol:
                        <input type="text" 
                            className="mdb-text" 
                            id={`${props.block}-protocol`} 
                            autoComplete={"off"} 
                            defaultValue={info.protocol} 
                        />
                    </label>                        
                    <label>
                        Before:
                        <input type="text" 
                            className="mdb-text" 
                            id={`${props.block}-when-before`} 
                            onChange={(e) => setBeforeQuery(e.target.value)}
                            onFocus={(e) => setBeforeQuery(e.target.value)}
                            ref={beforeRef}
                            autoComplete={"off"} 
                            defaultValue={info.when.before} 
                        />
                    <SearchResults list={matchesLabel(beforeQuery)} setVal={setBefore} />
                    </label>
                    <label>
                        After:
                        <input type="text" 
                            className="mdb-text" 
                            id={`${props.block}-when-after`} 
                            onChange={(e) => setAfterQuery(e.target.value)}
                            onFocus={(e) => setAfterQuery(e.target.value)}
                            ref={afterRef}
                            autoComplete={"off"} 
                            defaultValue={info.when.after} 
                        />
                    <SearchResults list={matchesLabel(afterQuery)} setVal={setAfter} />
                    </label>
                </div>
            </div>

            <div id="mdb-green" className="mdb-block" >
                <p className="mdb-block-heading" >Standard:</p>
                { [...Array(numGreen)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='standard' item={info} />
                )) }

                <div>
                    <button type="button" onClick={newGreen} >Add new</button>
                    <button type="button" onClick={lessGreen} >-</button>
                </div>
            </div>

            <div id="mdb-yellow" className="mdb-block" >
                <p className="mdb-block-heading" >Minimal:</p>
                { [...Array(numYellow)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='minimal' item={info} />
                )) }

                <div>
                    <button type="button" onClick={newYellow} >Add new</button>
                    <button type="button" onClick={lessYellow} >-</button>
                </div>
            </div>

            <div id="mdb-min-why" className="mdb-block" >
                <p className="mdb-block-heading" >Minimal reasoning:</p>
                <FormItem block={props.block} num={0} type='minimalWhy' item={info} />
            </div>

            <div id="mdb-red" className="mdb-block" >
                <p className="mdb-block-heading" >Contraindications:</p>
                { [...Array(numRed)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='contraindicated' item={info} />
                )) }

                <div>
                    <button type="button" onClick={newRed} >Add new</button>
                    <button type="button" onClick={lessRed} >-</button>
                </div>
            </div>

            <div id="mdb-contra-why" className="mdb-block" >
                <p className="mdb-block-heading" >Contraindication reasoning:</p>
                <FormItem block={props.block} num={0} type='contraindicatedWhy' item={info} />
            </div>

            <div id="mdb-findings" className="mdb-block" >
                <p className="mdb-block-heading" >Findings:</p>
                { [...Array(numFindings)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='findings' item={info} />
                )) }

                <div>
                    <button type="button" onClick={newFindings} >Add new</button>
                    <button type="button" onClick={lessFindings} >-</button>
                </div>
            </div>

            <div id="mdb-notes" className="mdb-block" >
                <p className="mdb-block-heading" >Notes / Comments:</p>
                { [...Array(numNotes)].map( (e, i) => (
                    <FormItem block={props.block} num={i} key={i} type='notes' item={info} />
                )) }

                <div>
                    <button type="button" onClick={newNotes} >Add new</button>
                    <button type="button" onClick={lessNotes} >-</button>
                </div>
            </div>
        </div>
    );
}

export default FormBlock;