import { useState, useRef, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import Header from '../Header';
import FormBlock from './FormBlock';
import { verifyDocument, publishDocument, getDocument } from '../../scripts/connect-firebase.js';
import criticalActions from '../../data/blank-crit-actions.json';

const COLLECTION_ID = 'scenario-required-actions';

const FormRoot = (props) => {
    const [mdbForm, setMDBForm] = useState(null);
    const [numBlocks, setNumBlocks] = useState(1);
    const { mdbFormId } = props.match.params;
    const formRef = useRef(null);
    const isEdit = () => !!mdbFormId;
    let history = useHistory();

    useEffect(() => {
        mdbFormId ?
        getDocument(COLLECTION_ID, mdbFormId)
            .then(res => {
                setMDBForm(res)
                setNumBlocks(res.criticalActions.length)
            })
            .catch(err => console.error(err))
        : setMDBForm({
            scenarioName: "",
            criticalActions: [
                criticalActions
            ]
        });
    }, [mdbFormId])

    const addBlock = () => {
        let newForm = {...mdbForm};
        newForm.criticalActions.push(criticalActions);
        setMDBForm(newForm);
        setNumBlocks(numBlocks + 1);
    }

    const removeBlock = () => {
        let newForm = {...mdbForm};
        newForm.criticalActions.pop();
        setMDBForm(newForm);
        setNumBlocks(numBlocks - 1);
    }

    return (
        <>
        <Header />
        <div className="mdb-form-wrapper" >
            {
            mdbForm ? 
            <>
            <form id="debriefer-form" className="debriefer-form" ref={formRef} >
                <label className="mdb-block-heading">
                    Scenario Name:
                    <input type="text" className="mdb-text" id="scenarioName" autoComplete={"off"} defaultValue={mdbForm.scenarioName} />
                </label>

                { [...Array(numBlocks)].map( (e, i) => (
                    <FormBlock key={i} block={i} form={mdbForm} />
                )) }

                <div>
                    <button type="button" onClick={addBlock} >Add new</button>
                    <button type="button" onClick={removeBlock} >-</button>
                </div>
            </form>
            <button type="button" id="submit-bttn" onClick={process}>Submit</button>
            </>
            : <p>Loading...</p>
            }
        </div>
        </>
    );

    async function process() {
        let form = scan(formRef.current, []);
        let object = { scenarioName: null, criticalActions: [] };

        for (let i=0; i < form.length; i++) {
            let item = form[i], path = item.id.split('-');
            // path[0] = block, path[1] = type, path[2] = number

            if (path.length > 1 && !object.criticalActions[parseInt(path[0])]) 
                object.criticalActions.push( deepCopy(criticalActions) );

            switch (path.length) {
                case 1: // title
                    object[path[0]] = item.value;
                    break;

                case 2: // block metadata
                    object.criticalActions[parseInt(path[0])][path[1]] = item.value;
                    break;

                case 3: // block elements
                    if (path[1] === 'contraindications') {
                        if (form[i + 1].checked)
                            object.criticalActions[parseInt(path[0])].required.push(item.value);
                        else // default
                            object.criticalActions[parseInt(path[0])].contraindicated.push(item.value);
                        object.criticalActions[parseInt(path[0])].contraReqOrder.push(item.value);
                        i += 2;
                    } else {
                        object.criticalActions[parseInt(path[0])][path[1]][path[2]] = item.value;
                    }
                    break;

                default:
                    console.error(`Path length error @ element ${i}.`);
                    break;
            }
        }

        console.log(object)

        // Validate & upload object
        if (object.scenarioName.trim() === "" || (!isEdit && await verifyDocument(COLLECTION_ID, object.scenarioName.trim()))) {
            alert("You need a new name for the scenario!");
            return;
        } else {
            publishDocument(COLLECTION_ID, object.scenarioName, object);
            alert(`Debriefer info for ${object.scenarioName} uploaded.`);
            history.goBack();
        }
    }

    function scan(obj, arr) {
        for (let node of obj) 
            if (node.nodeName === 'INPUT')
                arr.push(node)

        return arr;
    }

    function deepCopy(obj) { return JSON.parse(JSON.stringify(obj)); }
}

export default FormRoot;